body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.select {
    margin-left: 10px;
}

.form {
    margin-top: 30px;
}

.span {
    margin-left: 5px;
}
@media print {
    .noprint {display:none}
    .form-control {
        border: none; 
        background-image:none;
        -webkit-appearance: none;
           -moz-appearance: none;
                appearance: none;
        text-align: center;
    }

    .form-control-select {
        padding-left: 40%;
    }

    .summary {
        text-align: center; 
  }
  
}

.shopcart-app {
  margin: 2rem auto 0 auto;
  max-width: 800px;
}

h1 {
  display: none;
}

.discount {
  display: none;
}




@media print {
  .noprint {display:none}

  .form-control-select {
    padding-left: 40%;
  } 

  th {
    text-align: center;
  }

  h1 {
    display: block;
    text-align: center;
  }

  .discount {
    display: block;
  }
  
  .table {
    margin-top: 30px;
  }
  
  .shopcart-app {
    width: 600px;
  }

  .table td {
    text-align: center;
  }

}

.table td {
  vertical-align: middle;
}


@page {
  margin: 0;
}



